@import url(https://fonts.googleapis.com/css?family=Archivo+Black);
.nf {
  border: 1px solid #000;
  padding: 6px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 12px;
  line-height: 14px;
  text-align: left;
}
.nf-title, .nf-serving-unit-qty, .nf-highlight, .nf-serving, .nf-amount-per-serving, .nf-calories {
    font-family: 'Archivo Black', sans-serif;
}
.nf-title {
    font-size: 2.15em;
    line-height: 1.15em;
    margin-top: -6px;
}
.nf-per-container {
    font-size: 1.15em;
    line-height: normal;
}
.nf-serving {
    font-size: 1.2em;
    line-height: normal;
}
.nf-amount-per-serving {
    font-size: .94em;
}
.nf-arrow-up, .nf-arrow-down {
    width: 0;
    height: 0;
    border-style: solid;
}
.nf-arrows {
    float: left;
    padding: 6px 5px 0 0;
}
.nf-arrow-up {
    cursor: pointer;
    margin-bottom: 4px;
    border-width: 0 5px 6px 5px;
    border-color: transparent transparent #333 transparent;
}
.nf-arrow-down {
    cursor: pointer;
    margin-top: 4px;
    border-width: 6px 5px 0 5px;
    border-color: #333 transparent transparent transparent;
}
.nf-modifier-field {
    width: 26px;
    text-align: center;
    line-height: normal;
    border: 1px solid #666;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1em;
    float: left;
    padding: 2px 0;
    margin-top: 3px;
}
.nf-item-name {
    margin-left: 50px;
    line-height: 1.25em;
    padding-top: 6px;
    padding-bottom: 5px;
}
.nf-serving-unit-qty {
    display: inline-block;
}
.nf-calories {
    font-size: 2em;
    line-height: 1em;
}
.nf-percent-dv {
    font-size: .84em;
}
.nf-vitamin-column {
    font-size: .94em;
    padding: 1px 0;
    border-bottom: 1px solid #000;
}
.nf-ingredient-statement {
    padding-top: 8px;
}
.nf-footnote {
    padding-top: 4px;
    margin-left: 5px;
    font-size: .85em;
    line-height: 1em;
}
.nf-footnote:before {
    content: "*";
    float: left;
    margin-left: -5px;
}
.nf-text-right {
    text-align: right;
}
.nf-line {
    border-top: 1px solid #000;
    padding-top: 1px;
    padding-bottom: 1px;
    font-size: .94em;
}
.nf-indent2 {
    margin-left: 28px;
}
.nf-indent {
    padding-left: 16px;
}
.nf-pr {
    float: right;
}
.nf-bar1 {
    height: 5px;
}
.nf-bar2 {
    height: 10px;
}
.nf-bar1, .nf-bar2 {
    background-color: #000;
}
.clear:before, .clear:after {
  content: " ";
  display: table;
}
.clear:after {
  clear: both;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
}
